import { fetchEndpoint } from '../server/network'

export const getVersionList = (page, size = 10) =>
  fetchEndpoint('/admin/api.appPackage.list', 'POST', {
    page: page,
    size: size
  })

export const addVersion = params => fetchEndpoint('/admin/api.appPackage.add', 'POST', params)

export const getSimpleVersion = id =>
  fetchEndpoint('/admin/api.appPackage.info', 'POST', {
    id: id
  })
