<template>
  <div class="add-edit-wrapper">
    <page-title :first-title="title"></page-title>
    <div class="version-info-wrapper">
      <el-form v-loading="loading" label-width="100px">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="更新类型">
              <el-select v-model="versionInfo.type" clearable>
                <el-option v-for="type in typeList" :key="type.id" :label="type.name" :value="type.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6" v-if="isAndroid">
            <el-form-item label="apk包名">
              <el-input clearable v-model="versionInfo.apk_name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6" v-if="isAndroid">
            <el-form-item label="apk包地址">
              <el-input clearable v-model="versionInfo.apk_url"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6" v-if="isAndroid">
            <el-form-item label="包大小">
              <el-input clearable v-model="versionInfo.apk_size"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20" v-if="isCommon">
          <el-col :span="6">
            <el-form-item label="版本号">
              <el-input clearable v-model="versionInfo.version_name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="版本码">
              <el-input clearable v-model="versionInfo.version_code"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="图片路径">
              <el-input clearable v-model="versionInfo.update_image"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="标题">
              <el-input clearable v-model="versionInfo.update_title"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20" v-if="isCommon">
          <el-col :span="6">
            <el-form-item label="弱更新版本">
              <el-input clearable v-model="versionInfo.update_version1"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="正常更新版本">
              <el-input clearable v-model="versionInfo.update_version2"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="强更新版本">
              <el-input clearable v-model="versionInfo.update_version3"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6"> </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="6">
            <el-form-item label="安卓md5" v-if="isAndroid">
              <el-input clearable v-model="versionInfo.apk_md5"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6" v-if="isIos">
            <el-form-item label="iOS升级的url">
              <el-input clearable v-model="versionInfo.update_url"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="6" v-if="$route.query.id && isCommon">
            <el-form-item label="更新状态">
              <el-select v-model="versionInfo.status" clearable>
                <el-option
                  v-for="status in statusList"
                  :key="status.id"
                  :label="status.name"
                  :value="status.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="更新内容">
              <el-input type="textarea" clearable v-model="versionInfo.update_content"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </div>
    <div class="btn-wrapper">
      <el-button type="primary" @click="saveVersion">保存</el-button>
      <el-button type="default" @click="$router.go(-1)">返回</el-button>
    </div>
  </div>
</template>

<script>
import { TYPE, STATUS } from './static'
import { addVersion, getSimpleVersion } from '../../api/appversion'
export default {
  name: 'AddandEditVer',
  data() {
    return {
      id: this.$route.query.id || '',
      versionInfo: {
        id: '',
        apk_md5: '',
        apk_name: '',
        apk_size: '',
        apk_url: '',
        status: '',
        type: '',
        update_content: '',
        update_image: '',
        update_title: '',
        update_url: '',
        update_version1: '',
        update_version2: '',
        update_version3: '',
        version_code: '',
        version_name: ''
      },
      typeList: TYPE,
      statusList: STATUS,
      loading: false
    }
  },
  computed: {
    title() {
      return this.id ? '编辑版本' : '新增版本'
    },
    isAndroid() {
      return this.versionInfo.type === 1
    },
    isIos() {
      return this.versionInfo.type === 2
    },
    isCommon() {
      return this.versionInfo.type === 1 || this.versionInfo.type === 2
    }
  },
  created() {
    if (this.id) {
      this.loading = true
      getSimpleVersion(this.id)
        .then(
          res => {
            this.versionInfo = { ...res }
          },
          err => {
            this.$message.error(err.errorMsg)
          }
        )
        .finally(() => {
          this.loading = false
        })
    }
  },
  methods: {
    saveVersion() {
      if (this.versionInfo.update_url.length > 100) {
        this.$message.error('iOS升级的url长度不能超过100')
        return
      }
      this.loading = true
      addVersion(this.versionInfo)
        .then(
          res => {
            this.$message.success('保存成功')
            this.$router.go(-1)
          },
          err => {
            this.$message.error(err.errorMsg)
          }
        )
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.add-edit-wrapper {
  min-width: 1290px;
}
.version-info-wrapper {
  margin-top: 20px;
  /deep/ .el-input {
    width: 200px;
  }
}
.btn-wrapper {
  margin-top: 20px;
  text-align: center;
}
</style>
